<script>
	import { mapMutations } from 'vuex'
	export default {
		globalData: {
		    statusBarHeight: 0, //状态栏高度
		    navHeight: 0, // 状态栏高度 + 导航栏高度
		    navigationBarHeight: 0, // 导航栏高度(标题栏高度)
		},
		onLaunch: function() {
			console.warn('当前组件仅支持 uni_modules 目录结构 ，请升级 HBuilderX 到 3.1.0 版本以上！')
			console.log('App Launch')
			
			let systemInfo = uni.getSystemInfoSync()
			// #ifdef MP-WEIXIN
			// 状态栏高度
			this.globalData.statusBarHeight = systemInfo.statusBarHeight
			// 获取微信胶囊的位置信息 width,height,top,right,left,bottom
			const custom = wx.getMenuButtonBoundingClientRect()
			this.globalData.customHeight = custom.height
			// 导航栏高度(标题栏高度) = 胶囊高度 + (顶部距离 - 状态栏高度) * 2
			this.globalData.navigationBarHeight = custom.height + (custom.top - this.globalData.statusBarHeight) * 2
			// 总体高度 = 状态栏高度 + 导航栏高度
			this.globalData.navHeight = this.globalData.navigationBarHeight + this.globalData.statusBarHeight
			// #endif
			

			// #ifdef H5
		    this.globalData.statusBarHeight = 0
		    this.globalData.navHeight = systemInfo.statusBarHeight + 45
		    // #endif
			
		},
		methods:{
			...mapMutations(['setToken','setUserInfo','setGoodsGroup','setHistory','setCartGoodsList','setCollectList','setAddressList'])
		},
		onShow: function() {
			console.log('App Show')
			// 刷新页面重新赋值vuex数据
			if(uni.getStorageSync('userInfo')){
				let data = {
					'accessToken':uni.getStorageSync('accessToken'),
					'refreshToken':uni.getStorageSync('refreshToken')
				}
				let userInfo = uni.getStorageSync('userInfo')
				let goodsGroup = uni.getStorageSync('goodsGroup')
				let history = uni.getStorageSync('history')
				let cartGoodsList = uni.getStorageSync('cartGoodsList')
				let collectList = uni.getStorageSync('collectList')
				let addressList = uni.getStorageSync('addressList')
				this.setToken(data)
				this.setUserInfo(userInfo)
				this.setGoodsGroup(goodsGroup)
				this.setHistory(history)
				this.setCartGoodsList(cartGoodsList)
				this.setCollectList(collectList)
				this.setAddressList(addressList)
			}
		},
		onHide: function() {
			console.log('App Hide')
		}
	}
</script>

<style lang="scss">
	/*每个页面公共css */
	@import '@/uni_modules/uni-scss/index.scss';
	/* #ifndef APP-NVUE */
	@import '@/static/customicons.css';
	/* #endif */
	view,image,input,ul{
		margin: 0;
		padding: 0;
		border: 0;
		box-sizing: border-box;
	}
	button{
		margin: 0;
		&::after{
			border: 0;
		}
	}
	.line{
		overflow: hidden;
	    text-overflow: ellipsis;
		white-space: nowrap;
	}
	.line2{
		display: -webkit-box;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 2;
		overflow: hidden;
	}
	page {
		background-color: #f5f5f5;
	}
	.container{
		width: 100%;
	}
	.icon-info-box{ // 角标
		position: relative;
		.info{
			width: 30upx;
			height: 30upx;
			line-height: 30upx;
			text-align: center;
			border: 50%;
			background-color:red ;
			position: absolute;
			top: -4upx;
			right: 18upx;
			font-size: 18upx;
			color: #fff;
			border-radius: 50%;
		}
	}
	.no-content{
		height: calc(100vh - 44px);
		display: flex;
		align-items: center;
		justify-content: center;
		.content{
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: center;
			margin-top: -200upx;
			.icon{
				width: 200upx;
				height: 200upx;
			}
			.text{
				font-size: 28upx;
				color:$uni-secondary-color ;
				margin-top: 20upx;
			}
		}
	}
	/* #ifdef MP-WEIXIN */
	.no-content{
		height: 100vh;
	}
	/* #endif */
	
</style>
